* {
  padding: 0;
  margin: 0;
}

.full-edit-mode header {
  display: none;
}
.full-edit-mode .app-wrapper {
  margin: 0 auto;
}

.navbar {
  font-size: 1.5rem;
}
.navbar > li {
  list-style: none;
  display: inline-block;
  width: 3rem;
  padding: 5px 10px;
}
.navbar > li .active > a {
  color: #fff;
  background: #454545;
}
.navbar > li .down {
  display: none;
  position: absolute;
  border: 1px solid #767766;
  background: #fff;
  color: #333;
  z-index: 9999;
}
.navbar > li .down > li {
  border-bottom: 1px solid #767766;
  padding: 0 5px 0 30px;
  position: relative;
  cursor: pointer;
}
.navbar > li .down > li:hover {
  color: #fff;
  background: #7e8;
}
.navbar > li .down > li i {
  margin-right: 1rem;
  position: absolute;
  left: 10px;
  line-height: 30px;
}
.navbar > li.open .down {
  display: block;
}
.navbar > li > div,
.navbar > li > a {
  text-align: center;
  text-decoration: none;
  color: #fff;
}
.navbar > li > div span,
.navbar > li > a span {
  display: block;
  font-size: 0.7rem;
}
.navbar.min > li > div span {
  display: none;
}

.top-nav {
  background: #767766;
  color: #fff;
  margin: -8px -8px 0 0;
}
.top-nav .navbar {
  font-size: 1.5rem;
}

#page-box {
  position: relative;
}

#mainMobile {
  border: 1px solid #767676;
  margin: 0 auto;
  position: relative;
  bottom: 0;
  top: 0;
  min-height: 500px;
  background-color: #fcc;
  padding: 20px 0 0 20px;
}
#mainMobile .top-rule {
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  height: 20px;
  width: 100%;
  background-color: #ccc;
}
#mainMobile .left-rule {
  width: 20px;
  top: 0;
  left: 0;
  bottom: 0;
  position: absolute;
  height: 100%;
  background-color: #ccc;
}
#mainMobile .rule-box {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#mainMobile #mainGrid {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: #fff;
}
#mainMobile #mainGrid.preview .row {
  min-height: auto;
}
#mainMobile #mainGrid.preview .weight-edit-grid .weight-action {
  display: none;
}
#mainMobile #mainGrid::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
#mainMobile #mainGrid::-webkit-scrollbar-track {
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
#mainMobile #mainGrid::-webkit-scrollbar-thumb {
  background-color: #aaa;
  -webkit-border-radius: 2em;
  -moz-border-radius: 2em;
  border-radius: 2em;
}
