.ui-draggable-dragging .action,
.ui-draggable-dragging .view {
  display: block;
}

.expand-box .expand-header {
  background: #efefef;
  font-size: 1.3rem;
  line-height: 30px;
}
.expand-box .expand-header .fa {
  float: right;
}
.expand-box .expand-body {
  display: none;
}
.expand-box.open .expand-header .fa {
  transform: rotate(180deg);
}
.expand-box.open .expand-body {
  display: block;
}

.card {
  width: 150px;
  height: 200px;
  float: left;
  margin: 5px;
  position: relative;
  border: 1px solid;
  overflow: hidden;
}
.card .card-logo img {
  width: 150px;
  height: 200px;
}
.card .card-body {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  min-height: 80px;
  background: rgba(255, 255, 255, 0.6);
}
.card .card-body h3 {
  text-align: center;
}
.card .card-action {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
}
.card.card-add .card-logo {
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 100px;
  height: 100px;
  margin: 50px auto 0;
  text-align: center;
  font-size: 75px;
}
.card.card-add:hover .card-logo {
  border: 1px solid rgba(56, 243, 250, 0.1);
  color: #fcc;
}
.card:hover {
  border: 1px solid burlywood;
}
.card:hover .card-body {
  background: rgba(255, 255, 255, 0.8);
}
.card:hover .card-action {
  display: block;
}

.theme-select .theme-item,
.theme-select .page-item,
.page-select .theme-item,
.page-select .page-item {
  display: inline-block;
  text-align: center;
  margin: 5px;
}
.theme-select .theme-item .thumb,
.theme-select .page-item .thumb,
.page-select .theme-item .thumb,
.page-select .page-item .thumb {
  height: 80px;
  width: 80px;
}
.theme-select .theme-item .thumb img,
.theme-select .page-item .thumb img,
.page-select .theme-item .thumb img,
.page-select .page-item .thumb img {
  width: 100%;
  height: 100%;
}
.theme-select .theme-item.active,
.theme-select .page-item.active,
.page-select .theme-item.active,
.page-select .page-item.active {
  border: 1px solid red;
}
.theme-select .theme-item:hover,
.theme-select .page-item:hover,
.page-select .theme-item:hover,
.page-select .page-item:hover {
  box-shadow: 3px 3px 4px rgba(33, 33, 33, 0.7);
}

.theme-select {
  max-height: 600px;
  overflow-y: auto;
}

.theme-dialog .dialog-body {
  overflow: unset;
}

.dialog-body-box {
  overflow-y: auto;
  background-color: #eee;
  max-width: 600px;
  max-height: 400px;
}
.dialog-body-box .item {
  text-align: center;
  display: inline-block;
  background-color: #fff;
  margin: 3px;
  position: relative;
}
.dialog-body-box .item .thumb {
  width: 100px;
  height: 100px;
}
.dialog-body-box .item .thumb img {
  width: 100%;
  height: 100%;
}
.dialog-body-box .item .name {
  width: 100px;
  overflow: hidden;
  color: #333;
  font-size: 12px;
  height: 16px;
}
.dialog-body-box .item.selected {
  background-color: rgb(174, 255, 238);
}
.dialog-body-box .item.selected::after {
  position: absolute;
  font-size: 100px;
  color: rgba(255, 0, 0, 0.3);
  font-family: "Font Awesome 5 Free";
  content: "\f00c";
  font-weight: 900;
  top: 10px;
  left: 0;
}
.dialog-body-box .dialog-pager {
  text-align: center;
}

.custom-config-view .multiple-box .item {
  border: 1px dotted #ccc;
  margin-bottom: 5px;
  position: relative;
}
.custom-config-view .multiple-box .item .multiple-del-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: red;
  color: white;
  line-height: 20px;
  text-align: center;
}

.custom-config-view {
  max-height: 80vh;
}